@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  min-height: 100vh;
  background: linear-gradient(125deg, red, orange);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container {
  width: 100%;
  max-width: 420px;
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
}

.title {
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  font-size: 30px;
  font-weight: bold;
  display: flex;
}

.title img {
  width: 40px;
  height: 40px;
  margin-left: 10px;
}

.todo-list-bar {
  width: 100%;
  height: 35px;
  background-color: #dcdcdc;
  border-radius: 20px;
  justify-content: space-between;
  display: flex;
}

input {
  background-color: #0000;
  border: none;
  border-radius: 10px;
  outline: none;
  flex: 1;
  padding: 10px 20px;
}

.addBtn {
  cursor: pointer;
  color: #fff;
  background-color: #ff3939;
  border: none;
  border-radius: 20px;
  outline: none;
  padding: 5px 20px;
  font-size: 15px;
}

ul {
  padding: 10px;
}

ul li {
  cursor: pointer;
  padding: 5px 5px 5px 35px;
  font-size: 15px;
  list-style: none;
  position: relative;
}

ul li:before {
  content: "";
  height: 16px;
  width: 16px;
  background-image: url("unchecked.2352d3af.png");
  background-position: center;
  background-size: cover;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(50%, -50%);
}

ul li.checked {
  text-decoration: line-through;
}

ul li.checked:before {
  background-image: url("checked.461b5506.png");
  background-position: center;
  background-size: cover;
  top: 50%;
  left: 0;
  transform: translate(50%, -50%);
}

span {
  width: 20px;
  height: 20px;
  cursor: pointer;
  border-radius: 50%;
  padding-left: 4.5px;
  font-size: 16px;
  line-height: 21px;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(-50%, -50%);
}

span:hover {
  background: #dcdcdc;
}

